*{
    margin: 0;
    padding: 0;
}
.App{
    width: 100vw;
    height: 100vh;
    background-color: lightblue;
} 
.container{
    width: 100%;
    height: 640px;
    display: flex;
}
form{
    padding-top: 30px;
    padding-left: 300px;
}
.formButton{
    width: 200px;
    height: 50px;
    border-radius: 4px;
    background-color: grey;
    color: white;
    font-size: 20px;
}

.navbar{
    width: 100%;
    height: 65px;
    background-color: lightblue;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}
.navbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    font-weight: bold;
    font-size: 35px;
    padding-left: 16px;
    color: darkblue;
    cursor: pointer;
}
.topRight{
    display: flex;
    align-items: center;
}
.navbarIconContainer{
    position: relative;
    cursor: pointer;
    margin-right: 10px;
    color: #555;
}
.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute;
    top:-5px;
    right: 5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}
.topAvatar{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    cursor: pointer;
}
.Sidebar{
    height: 100%;
    width: 250px;
    background-color: #2f4050;
    position: -webkit-sticky;
    position: sticky;
}
.SidebarList{
    height: 630px;
    padding: 0;
    width: 100%;
}
.SidebarList .row{
    width: 100%;
    height: 50px;
    list-style-type: none;
    margin: 0;
    display: flex;
    flex-direction: row;
    color: white;
    justify-content: center;
    align-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.SidebarList .row:hover{
    cursor: pointer;
    background-color: #293846;
}
.SidebarList #active{
    background-color: #293846;
}
.row #icon{
    flex: 30% 1;
    display: grid;
    place-items: center;
}
.row #title{
    flex: 70% 1;
}
.courseList{
    border: 2px solid;
    border-radius: 10px;
    margin-left: 40px;
    margin-top: 30px;
    margin-bottom: 110px;
}
.listItem{
    border: 1px solid;
    border-radius: 5px;
    padding-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
}
.studentinfotitle{
    position: relative;
    font-size: 40px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    left: 30px;
    top: 20px;
    border: 2px solid;
    border-radius: 10px;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 15px;
}
.maincard{
    width: 1250px;
    height: 500px;
    margin-top: 55px;
    display: flex;
    position: relative;
    left: 30px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border: 2px solid;
    border-radius: 10px;
    border-color: black;
}
.profileLeft{
    width: 350px;
    padding: 30px;
    border: 2px solid;
    border-radius: 10px;
    margin: 30px;
}
.profileImg{
    width: 150px;
    height: 150px;
    margin-top: 25px;
    margin-bottom: 30px;
    border-radius: 50%;
    cursor: pointer;
}
.leftInfoText{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 15px;
    padding: 3px;
}
.profileRight{
    width: 700px;
    font-size: 20px;
    display: flex;
    padding-top: 30px;
    margin: 30px;
    padding-left: 50px;
    padding-right: 60px;
    border: 2px solid;
    border-radius: 10px;
}

.rightInfoTextQue{
    padding-bottom: 20px;
    font-size: 15px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}
.rightInfoTexta{
    padding-left: 150px;
}
.rightInfoTextAns{
    padding-bottom: 20px;
    font-size: 15px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
